@import url(https://fonts.googleapis.com/css?family=Tangerine);
h1, h2, h3, h4, h5, h6 {
        font-family: "Playfair Display", serif;
        color: #000;
      }
.food-banner {
        position: relative;
        font-size:50px;
        width: 100%;
        height:1000px;
}
.active {
        display: block;
}
.hide {
        display: none;
}

.food-banner__images {
        width: 100%;
        height: inherit;
}
.food-banner__images li img{
        background-repeat: no-repeat;
        background-size: cover;
        width: 100%;
        height: 616px;
}
.food-banner__titles {
        position: absolute;
        top: 25%;
        text-align: center;
        width: 100%;
}
.food-banner__title1{
        color: white;
        font-weight: 1000;
}
.food-banner__title2 {
        color: orange;
}
.to-animate {
        -webkit-animation: fade-in 3s ease-out,forwards;
                animation: fade-in 3s ease-out,forwards;
}

@-webkit-keyframes fade-in {
        from {
                opacity: 1;
                -webkit-transform: translate3d(0, 50px, 0);
                transform: translate3d(0, 50px, 0);
        }
        to {
                opacity: 0;
                -webkit-transform: none;
                transform: none;
        }
}

@keyframes fade-in {
        from {
                opacity: 1;
                -webkit-transform: translate3d(0, 50px, 0);
                transform: translate3d(0, 50px, 0);
        }
        to {
                opacity: 0;
                -webkit-transform: none;
                transform: none;
        }
}
body{
        margin:0;
        padding: 0;
}
ul, li, h1, h2, h3, h4, h5, h6 {
        list-style-type: none;
        list-style: none;
        margin:0 auto;
        padding:0
        ;-webkit-margin-before: 0em;margin-block-start: 0em;
        -webkit-margin-after: 0em;
                margin-block-end: 0em;
        -webkit-margin-start: 0px;
                margin-inline-start: 0px;
        -webkit-margin-end: 0px;
                margin-inline-end: 0px;
}

.banner-container {
        border: 0 solid;
        font-size: 16px;
        height: 100vh;
        width: calc(100%);
        font-family: 'Volkorn', 'Crimson Text', sans-serif;
}

.banner-words {
        display:  flex;
        flex-direction: row;
        flex-shrink: 0;
        font-size: 1rem;
        line-height: 1.5;
        position: relative;
        height:11.25rem;
        letter-spacing: 2px;
        background-color: #00B6FF;
}
.hide {
        display: none;
}
.active {
        display: block;
}
.banner-title {
        overflow: hidden;
        flex-grow: 1;
        text-align: center;
        align-items: center;
        justify-content: flex-start;
        padding: 1.25rem;
        padding: 1.25rem;
        line-height: 5.5;
        background-color: aliceblue;
}

.banner-title:first-child {
        background-color: #00B6FF;
}

.banner-sliders {
        background-color: rgba(0,0,0,.1);
        position: relative;
        display: flex;
        flex-direction: row;
        height: calc(100vh - 11.25rem);
        overflow: hidden;
}
.banner-images {
        position: absolute;
        top:0;
        left: 0;
        width: 100%;
        height: 100%;
        cursor: -webkit-grabbing;
        cursor: grabbing;
}
.banner-images li{
        width: 100%;
        height: 100%;
}
.banner-images li img{
        width: 100%;
        height: 100%;
}
.banner-introduction {
        height: 12rem;
        position: absolute;
        right:0;
        bottom: 0;
        padding-left: 1.25rem;
        display: flex;
        flex-direction: row;
        width: calc(45% - 5.5rem);
        box-shadow: 2px 2px 2px aliceblue;
        background-color: aliceblue;
}

.banner-introduction__group {
        padding-right: 3rem;
        padding-left: 1.5rem;
        padding-top: 2.5rem;
        width:100%;
        position: relative;
        display: flex;
        flex-direction: row;
}
.banner-descriptions li {
        background-color: aliceblue;
        text-align: left;
        width: 100%;
        margin-right: 20px;
}

.banner-descriptions li div h6 {
        margin-bottom: 0.5rem;
        font-size: 2.125em;
        position: relative;
        line-height: 1;
        margin-bottom: 1rem;
        font-style: italic;
        font-family: 'Crimson Text',serif;
}

@-webkit-keyframes fadeBackground {
        from {
                -webkit-transform: translateY(-100%);
                        transform: translateY(-100%);
                background-color: aliceblue;
        }
        to {
                -webkit-transform: translateY(0);
                        transform: translateY(0);
                background-color: cornsilk;;
        }
}

@keyframes fadeBackground {
        from {
                -webkit-transform: translateY(-100%);
                        transform: translateY(-100%);
                background-color: aliceblue;
        }
        to {
                -webkit-transform: translateY(0);
                        transform: translateY(0);
                background-color: cornsilk;;
        }
}

.banner-descriptions li div {
        font-weight: 500;
        font-size: 1.125rem;
        width: 22rem;
}

.banner-dots {
        position: relative;
        margin-left:12px;
        top:8px;
        height: 8rem;
        flex-direction: column;
        display: flex;
}
.banner-dots li {
        width: 16px;
        height: 16px;
        border-radius: 50%;
        margin:5px auto;
        border:1px solid grey;
        -webkit-transition: 0.3s ease;
        transition: 0.3s ease;
}
.banner-dots .active {
        background-color: black;
}
.banner-dots li:hover {
        border: 1px solid black;
}

.header {
        display: flex;
        flex-direction: row;
        position: fixed;
        height: calc(1vh*100);
        height: calc(var(--vh, 1vh)*100);
        z-index: 50;
        width: 100%;
        height: 100%;
        left:0;
        top: 0;
}
a {
        text-decoration: none;
        color: black;
        display: inline-block;
}
.side {
        z-index: 60;
        height: 100%;
        position: relative;
        display: flex;
        flex-direction: row;
        background-color: #00B6FF;
}
.side-bar {
       height: 100%;
        position: relative;;
        -webkit-transition: color 0.6s ease;;
        transition: color 0.6s ease;
        align-items: stretch;
        width: 6.785rem;
        display: flex;
        flex-direction: column;
}

.sidebar-button {
        position: relative;
        height: 175px;
        width: 100%;
        display: flex;
        align-items: center;
        flex-direction: column;
        justify-content: center;
        cursor: pointer; 
}

.sidebar-button div {
        height:5px;
        width: 40%;
        background-color: #1d1d1d;
        margin-bottom: 8px;
}

.sidebar-button div:nth-child(2) {
        width: 25%;
        -webkit-transition: width .7s ease;
        transition: width .7s ease; 
}

.nav-menu {
        width: 0;
        position: relative;
        overflow:hidden;
        font-family: 'Times New Roman Special', Times, serif;
        flex-direction: row;
        color:black;
        background-color: #00B6FF;
}
.nav-show {
        display: flex;
}
.menu-category {
        padding-left: 5rem;
        padding-right: 5rem;
        padding-top:0;
        padding-bottom: 0;
        flex: 1 1;
        display: flex;
        flex-direction: row;
}

.menu-stretch {
        padding-left: 3.725rem;
        padding-right: 3.725rem;
        margin-top: 10.25rem;
        margin-bottom: 3rem;
        font-size: 4rem;
        display: flex;
        flex-direction: column;
        font-family: 'Crimson Text',serif;
}
.menu-item{
        display: flex;
        flex-direction: row;
        margin-bottom: 2rem;
}
.menu-category .menu-stretch ul {
        display: flex;
        flex-direction: row;
        font-size: 3rem;
        padding-left: 3.5rem;
}
.menu-category .menu-stretch ul li {
        line-height: 1.5;
        margin-right: 2rem;
}
.menu-category .menu-stretch ul li:nth-child(1) i {
        color: #0845c9;
}
.menu-category li:nth-child(2) i {
        color: #96c8db;
}
.menu-category li:nth-child(3) i {
        color: rgb(90, 18, 30);
}
.menu-category li:nth-child(5) i {
        color: rgb(228, 9, 46);
}
.menu-stretch a svg {
        height: 3rem;
        width: 3rem;
        padding: 0 2.25rem;
        margin-top: 1.25rem;
        font-size: 3rem;
}

.swiper-container {
        display: block;
        width: 25%;
        height: calc(100%);
        margin:0;
        opacity: 0;
        position: absolute;
        right:0;
        top:0;
}
.swiper-container li{
        min-width: 335px;
        position: absolute;
        width: 100%;
        height: 100%;
}
.take-cover {
        background-color: #00B6FF;
        z-index: 10;
}
.swiper-container li img{
        width: 100%;
        height: 100%;
}

